  helloMain = (e) => {
      e.preventDefault()
      const chatMessages = document.querySelector('#showtime');
      chatMessages.scrollTop = chatMessages.scrollHeight;
      let y = document.querySelector('#input2').value;
      let y1 = y;
      if (y.includes('∫')) {
          y1 = y1.replace('(',',(x,')
          y1 = y1.replace('∫','∫(')
          y1 = y1 +')'
          y1= y1.replace('∫','Integral');
          y1= y1 +'.doit()'
        }
      if (y.includes('lim')) {
          y1 = y1.replace(',',',x,')
          y1 =y1.replace('lim','Limit');
          y1= y1 + '.doit()'}
//∫ f (lb,ub)  lim(f, ->x) ∫(f,(x,lb,ub))  lim(f, x, ->x)
      const params = new URLSearchParams();
      params.append(y1,'value');

      console.log(y)
      this.setState({
          MySwitch:false
      })

      if (y1.includes("Integral") || y1.includes('Limit')) {
      //  y = y +'.doit()'
        axios.post("http://localhost:4000/",params)
        .then(response => {
            console.log(response.data)

            var node = document.createElement("LI");                 // Create a <li> node
            var textnode = document.createTextNode(y);         // Create a text node
            node.appendChild(textnode);                              // Append the text to <li>
            document.getElementById("messages").appendChild(node);

            var node = document.createElement("LI");                 // Create a <li> node
            var textnode = document.createTextNode(response.data);         // Create a text node
            node.appendChild(textnode);                              // Append the text to <li>
            document.getElementById("messages").appendChild(node);
            chatMessages.scrollTop = chatMessages.scrollHeight;

            this.setState({
                eq:response.data,
                eq2:y
            })

        }).catch((err) => {
            console.log(err)})
        }
         else {
        axios.post("http://localhost:4000/math",params)
        .then(response => {
            console.log(response.data);

            var node = document.createElement("LI");  
            var textnode = document.createTextNode(y);  
            node.appendChild(textnode);                              // Append the text to <li>
            document.getElementById("messages").appendChild(node);

            var node = document.createElement("LI");                 // Create a <li> node
            var textnode = document.createTextNode(response.data);         // Create a text node
            node.appendChild(textnode);                              // Append the text to <li>
            document.getElementById("messages").appendChild(node);
            chatMessages.scrollTop = chatMessages.scrollHeight;

            this.setState({
                eq:response.data,
                eq2:y
            })
        }).catch((err) => {
            console.log(err)})
    }
}
